import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { 
  Card, 
  Row, 
  Form, 
  Col, 
  // Checkbox, 
  Table,
  InputNumber,
  // Icon,
} from 'antd';
// import { CartHeadAction } from 'components/common';
import { getStaffProfilesByAges, exportStaffProfilesByAgesExcell } from 'services/staffProfile';
import { BtnExport, BtnSearch, BtnAddNew, BtnClear } from 'components/common/button';
import { makeid } from 'utils/functionUtil';
import './style.scss';

@Form.create({ name: 'formReportByAge' })
class ReportByAge extends Component {
  state = {
    loadingStudents: false,
    tableData: null,
    lastRequest: null,
    listSearchItem: [
      {
        fromAge: 0,
        toAge: 30,
        id: makeid(6),
      },
      {
        fromAge: 30,
        toAge: 40,
        id: makeid(6),
      },
      {
        fromAge: 40,
        toAge: 50,
        id: makeid(6),
      },
    ],
    columnsTable: [
      {
        title: 'Độ tuổi',
        dataIndex: 'rangeLabel',
        key: 'rangeLabel',
      },
      {
        title: 'Nam',
        dataIndex: 'noOfMan',
        key: 'noOfMan',
      },
      {
        title: 'Nữ',
        dataIndex: 'noOfWoman',
        key: 'noOfWoman',
      },
    ],
  };

  componentDidMount = async () => {
    console.log('componentDidMount');
  };

  componentWillReceiveProps = () => {
    console.log('componentWillReceiveProps');
  };

  handleExport = async () => {
    const { lastRequest } = this.state;
    await exportStaffProfilesByAgesExcell(lastRequest);
  };

  handleSearch = async () => {
    await this.props.form.validateFields(async(err, values) => {
      if (!err) {
        this.setState({
          loadingStudents: true,
        });
        const formatValues = [];
        const tempObjectValues = {};
        // format data to Object
        Object.keys(values).forEach(key => {
          const tempKeyVals = key.split('_');
          const id = tempKeyVals[0];
          const keyVal = tempKeyVals[1];
          if(tempObjectValues[id] === undefined) {
            tempObjectValues[id] = {};
          }
          tempObjectValues[id][keyVal] = values[key];
        })
        // format object to array
        Object.keys(tempObjectValues).forEach((key, index) => { 
          const tempObj = {
            row: index + 1,
            ...tempObjectValues[key],
          }
          formatValues.push(tempObj);
        });
        if (formatValues.length) {
          const data = await getStaffProfilesByAges({listFilter: formatValues});
          console.log('data',data);
          const formatData = data.data.items.map(item => ({
            ...item,
            customId: makeid(6),
          }));
          this.setState({
            tableData: formatData,
            loadingStudents: false,
            lastRequest: formatValues,
          });
        }
      }
    });
  };

  remove = (index) => {
    const { listSearchItem } = this.state;
    const tempList = [...listSearchItem];
    tempList.splice(index, 1);
    this.setState({
      listSearchItem: tempList,
    });
  };

  add = () => {
    const { listSearchItem } = this.state;
    const tempList = [...listSearchItem];
    tempList.push(
      {
        row: tempList.length + 1,
        fromAge: 0,
        toAge: 30,
        id: makeid(6),
      },
    );
    this.setState({
      listSearchItem: tempList,
    });
  };

  renderItems = () => {
    const { getFieldDecorator } = this.props.form;
    const {
      listSearchItem
    } = this.state;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 20 },
      },
    };

    const formItems = listSearchItem.map((item, index) => (
      <Row key={item.id}>
        <Col span={4}>
          {`Bộ lọc ${index+1}: `}
        </Col>
        <Col span={8}>
          <Form.Item
            {...formItemLayout}
            required={false}
            label='Tuổi từ:'
          >
            {getFieldDecorator(`${item.id}_fromAge`, {
              initialValue: item.fromAge,
              rules: [
                {
                  required: true,
                  message: "Tuổi không được trống.",
                },
              ],
            })(<InputNumber placeholder="Tuổi từ" />)}
          </Form.Item>
        </Col>
        <Col span={8}>
          <Form.Item
            {...formItemLayout}
            required={false}
            label='Tuổi đến:'
          >
            {getFieldDecorator(`${item.id}_toAge`, {
              initialValue: item.toAge,
              rules: [
                {
                  required: true,
                  message: "Tuổi không được trống.",
                },
              ],
            })(<InputNumber placeholder="Tuổi đến" />)}
          </Form.Item>
        </Col>
        <Col span={4}>
          <BtnClear title="Xóa" onClick={() => this.remove(index)} />
        </Col>
      </Row>
    ));
    return formItems;
  }

  render() {
    const { loadingStudents, tableData, columnsTable } = this.state;
    return (
      <Fragment>
        <Card title="Báo cáo theo độ tuổi">
          <Form onSubmit={this.handleSubmit}>
            {this.renderItems()}
            <div className='btn-group'>
              <BtnAddNew title="Thêm độ tuổi" onClick={this.add} />
              <BtnSearch title="Tìm kiếm" onClick={this.handleSearch} />
              <BtnExport title="Xuất Excel" onClick={this.handleExport} />
            </div>
          </Form>
          <Row>
            <Col span={24}>
              <Table
                bordered
                loading={loadingStudents}
                dataSource={tableData}
                columns={columnsTable}
                rowKey="customId"
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReportByAge />
  </Page>
);



